<template>
  <div class="container">
    <div class="dragonflys" v-for="item of items" :key="item.id">
      <ItemVue :item="item" @click="Pick(item)"></ItemVue>
    </div>
  </div>
</template>

<script>
import ItemVue from './ItemPart.vue'

export default {

    props: {
      items: Array
    },
    components: {
        ItemVue
    },

    methods: {
      Pick(dragon){
        this.$emit('Pick', dragon)
      }
    },

}
</script>

<style scoped>
  .container {
    width: 50%;
    height: 100%;
    border: 1px solid black;
    border-radius: 10px;
    overflow: auto;
    
    padding: 10px 2px;
    
  }

</style>