<template>
  <div class="register">
    <input type="text" class="login" placeholder="Логин" v-model="login">
    <input type="text" class="password" placeholder="Пароль" v-model="password">
    <button @click="Send" class="regBtn">Вход</button>
  </div>
</template>

<script>
import axios from "axios";

const axiosC = axios.create({
  withCredentials: true
})

export default {

  data() {
    return {
      login: "",
      password: ""
    }
  },

  methods:{
    Send(){
      console.log(this.login, this.password);
      axiosC.post("http://185.185.71.250:3000/api/auth/login",{
        login: this.login,
        password: this.password,
      })
        .then(() =>  this.$emit('reg',true))
        .catch(() => {})
    }
  }
}
</script>

<style scoped>

  .register {
    width:600px;
    height: 300px;
    border: 1px solid black;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:10px;
  }

  .login, .password {
    width: 40%;
    height: 20px;
  }

  .regBtn {
    width: 60px;
    height: 20px;
  }



</style>