<template>
  <div class="container">
    <p>{{item.stat_id}}</p>
    <img v-bind:src="`http://185.185.71.250:3000/api/admin/${item.image}/download`" alt="">
    <p>{{item.name}}</p>
    <div class="buttons">
      <button @click="CardEvent(true)">Согласиться</button>
      <button @click="CardEvent(false)">Отказ</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

const axiosC = axios.create({
  withCredentials: true
})
export default {
  props:{
    item: Object,
  },

  methods: {
    CardEvent(event)  {
      this.$emit("CardEvent", event);
      axiosC.post(`http://185.185.71.250:3000/api/admin/`,{
        veracity: event,
        ...this.item
      }).then(() => console.log("success")).catch(() => console.log("fail"))
    }
  }
}
</script>

<style scoped>
  .container {
    height: 90%;
    width: 30%;
    border: 1px solid black;
    border-radius: 15px;
    padding: 15px 3px;
    margin: 20px 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  img {
    max-height: 70%;
    max-width: 100%;
  }

  .buttons {
    width: 80%;
    display: flex;
    justify-content: space-between;
  }

</style>