<template>
  <div class="conteiner">
    <ListVue class="list" :items="items" @Pick="Pick"/>
    <CardVue class="card" v-bind:item="item" @CardEvent="CardEvent" v-show="item"  />
  </div>
</template>

<script>
import CardVue from './CardComp.vue'
import ListVue from './ListComp.vue'
import axios from "axios";

const axiosC = axios.create({
  withCredentials: true
})

export default {
  components: {
    CardVue, ListVue
  },

  data() {
    return {
      item: false,
      items:[]
    }
  },

  methods: {
    CardEvent(event){
      console.log(event);
      this.item = false;
    },
    Pick(dragon){
      this.item = dragon;
    }
  },

  mounted(){
    axiosC.get("http://185.185.71.250:3000/api/admin/")
    .then(res => {
      this.items = res.data;
    }
    ).catch(err => console.log(err))
  },
  computed(){
    axiosC.get("http://185.185.71.250:3000/api/admin/")
    .then(res => {
      this.items = res.data;
    }
    ).catch(err => console.log(err))
  }


}
</script>

<style scoped>

  .conteiner {
    width: 80vw;
    height: 70vh;


    display: flex;
    justify-content: space-between;
  }


</style>